<template>
  <div id="indexPage">
    <my-header></my-header>
    <el-main>
      <ul class="lj clearFixed">
        <li>
          <div class="icons"><i class="iconfont iconganranke"></i></div>
          <span>感染性</span>
          <span>{{todayList.ganran}}</span>
        </li>
        <li>
          <div class="icons"><i class="iconfont iconsunhuai"></i></div>
          <span>损伤性</span>
          <span>{{todayList.sunshang}}</span>
        </li>
        <li>
          <div class="icons"><i class="iconfont iconbingli"></i></div>
          <span>病理性</span>
          <span>{{todayList.bingli}}</span>
        </li>
        <li>
          <div class="icons"><i class="iconfont iconhuaxueshiyan"></i></div>
          <span>化学性</span>
          <span>{{todayList.huaxue}}</span>
        </li>
        <li>
          <div class="icons"><i class="iconfont iconyao"></i></div>
          <span>药物性</span>
          <span>{{todayList.yaowu}}</span>
        </li>
      </ul>
      <el-card id="card" class="box-card" style="max-height: 360px;">
        <div class="tongji">
          <div class="todaytotal">
            <!--<el-input ref="inputDept"  autofocus="true" v-model="deptId" @input="inputDeptId" style="width: 60%"></el-input>-->
            <el-input ref="inputDept"  autofocus="true" v-model="deptId"  style="width: 60%; visibility: hidden; display: none"></el-input>
            <!--<div class="tips" style="color: #999; font-size: 14px; margin-top: 10px;">扫描科室二维码</div>-->
            <div class="title" style="font-weight:bold;">今日统计</div>
            <div class="ganran">
              <!--<i class="iconfont iconganranke"></i>-->
              <span class="num">{{todayTotal.zzl}}</span>
              <span class="unit">KG</span>
            </div>
            <div class="ganran">
              <!--<i class="iconfont iconganranke"></i>-->
              <span class="num">{{todayTotal.zsl}}</span>
              <span class="unit">袋</span>
            </div>

          </div>
          <div class="deplist" style="max-height: 360px;overflow-y: scroll;">
            <ul>
              <li v-for="dep in deplist">
                <div v-on:click="goto(dep.depid,dep.name)">
                  <div class="d1">{{dep.name}}</div>
                  <div class="d2">{{dep.address}}</div>
                  <div class="num on">{{dep.num}}</div>
                </div>

              </li>
            </ul>
          </div>
          <div class="typetab">
            <el-button v-for="(key,index) in errType" v-on:click="selType(index,key.errtype)" :class="{on:seltype==index}" :key="index">{{key.name}}</el-button>
          </div>
        </div>
      </el-card>

    </el-main>
    <my-footer></my-footer>
  </div>
</template>

<script>
import myHeader from './header';
import myFooter from './footer';
import {_Widget} from "../common";
import {Downloader} from "../common";
import { Loading } from 'element-ui';
//import axios from "axios";
import ajax from "ajax";
export default {
  name: 'index',
  components:{
    myHeader,
    myFooter,
  },
  data: () => ({
    seltype:0,//选中序列默认值
    selErrtype:0,//选中的类型
    deptId:"",
    errType:[{
      name:"已处理",
      errtype:0
    },{
      name:"未处理",
      errtype:1
    }],
    todayList:{
      ganran:0,
      sunshang:0,
      bingli:0,
      huaxue:0,
      yaowu:0,
    },
    todayTotal:{
      zzl:0,
      zsl:0
    },
    deplist:[{
      depid:"12022",
      name:"超声科",
      address:"门诊楼1F",
      num:3
    }],
    print:{
      deptname:"",
      time:"",
      type:"",
      weight:"",
      hospital:"",
      code:"",
      person:""
    },
  }),
  methods: {
    loadingInstance(){
      return Loading.service({ fullscreen: false ,background:"rgba(0,0,0,0)"})
    } ,
    inputDeptId(event){
      var thisObject = this;
      var deplist = localStorage.getItem("deplist");
      if(deplist){
        deplist = JSON.parse(deplist);
      }
      this.deplistAll = this.deplistAll || deplist;
      if(this.deplistAll.length && this.deptId){
        this.deplistAll.forEach(function (dept){
          if(dept.depid == thisObject.deptId){
            thisObject.goto(dept.depid,dept.name);
          }
        })
      }
    },
    selType: function (index,errtype) {
      this.seltype = index;
      this.selErrtype = errtype;
      this.getDeptment(this.selErrtype)
    },
    goto(id,deptname){
      var url = "/depCollection?deptid=" + id + "&deptname=" + deptname;
      this.$router.push(url);
    },
    getDeptment(type){
      //type:0 已处理 1 未处理
      var thisObject = this;
          var url = thisObject.config.baseURL + "of_ksxx";
          url += "?hospitalCode=" + thisObject.config.hospitalCode;
          Downloader.loadXMLAsync(url, function(e) {
            var xmldoc = e.response;
            var obj = {};
            var xml = _Widget.xml.jsonToxml(xmldoc);
            var json = _Widget.xml.xmlToJson(xml);
            var res = (JSON.parse(json.string["#text"]));
            if(res.result == 1){
              thisObject.deplistAll = res.data;
              thisObject.deplist = [];
              res.data.forEach(function (item){
                if(type == 0){
                  if(item.num != 0){
                    thisObject.deplist.push(item);
                  }
                }else{
                  if(item.num == 0){
                    thisObject.deplist.push(item)
                  }
                }
              })
              localStorage.setItem("deplist",JSON.stringify(res.data));
              //thisObject.loading.close()
            }
          },function (err) {
            thisObject.$message({
              type: 'error',
              message: '网络链接失败!'
        });
        var deplist = localStorage.getItem("deplist")
        thisObject.deplist = JSON.parse(deplist);
      })
    },
    getData(){
      var thisObject = this;
      var url = thisObject.config.baseURL + "of_yf_flhz";
      url += "?hospitalCode=" + thisObject.config.hospitalCode;
      Downloader.loadXMLAsync(url, function(e) {
        var xmldoc = e.response;
        var obj = {};
        var xml = _Widget.xml.jsonToxml(xmldoc);
        var json = _Widget.xml.xmlToJson(xml);
        var res = (JSON.parse(json.string["#text"]));
        if(res.result == 1){
          thisObject.todayList = res.data;
          thisObject.todayTotal.zzl = res.zzl;
          thisObject.todayTotal.zsl = res.zsl;
          localStorage.setItem("todayTotal",JSON.stringify(thisObject.todayTotal))
          localStorage.setItem("todayList",JSON.stringify(thisObject.todayList))
        }
      },function (err) {
        thisObject.$message({
          type: 'error',
          message: '网络链接失败!'
        });
        var todayTotal = localStorage.getItem("todayTotal");
        var todayList = localStorage.getItem("todayList");
        thisObject.todayTotal = JSON.parse(todayTotal);
        thisObject.todayList = JSON.parse(todayList);
      })
    },
    onkeydown(e){
      var thisObject = this;
      if(e.keyCode == 13){
        thisObject.deptId = thisObject.keyval;
        thisObject.inputDeptId(thisObject.keyval);
        thisObject.keyval = '';
        return
      }
      thisObject.keyval += String.fromCharCode(e.keyCode);
    },

  },
  mounted() {
    var thisObject = this;
    this.config = JSON.parse(localStorage.getItem("config"));
    if(!this.config){
      this.$router.push('./settings');
    }
    this.getDeptment(0);
    this.getData();
    this.$refs.inputDept.focus();
    this.deptId = '';
    thisObject.keyval = "";
    document.addEventListener("keydown",this.onkeydown);
  },
  destroyed(){
    this.keyval = "";
    document.removeEventListener("keydown",this.onkeydown)
  }
}
</script>



