<template>
  <div style="position: fixed;width: 100%;top:0;z-index:9;">
    <el-header id="header" class="clearFixed">
      <span id="logo">
        <img style="height: 40px" src="../assets/logo.png" alt="">
      </span>
      <span class="car">{{config.carCode}}车</span>
      <span class="doctor"><i class="el-icon-user"></i>操作员：{{operator}}</span>
      <span class="doctor"><i class="el-icon-user"></i>交接人：{{changePersion}}</span>
      <el-button id="logout"  plain icon="el-icon-close" v-on:click="logout()">退出</el-button>
      <el-button id="setting" plain icon="el-icon-setting" v-on:click="goto('./Login2?act=settings')">系统设置</el-button>
      <span class="syncData" @click="syncData"><i v-show="!hasData" class="iconfont iconyunshangchuan_o"></i><i v-show="hasData" class="iconfont blue iconyunshangchuan_o"></i></span>
      <span class="bluetooth" @click="conntentBluetooth"><i v-show="!isBluetooth" class="iconfont iconbluetooth-b"></i><i v-show="isBluetooth" class="iconfont iconbluetooth"></i></span>
    </el-header>
  </div>
</template>
<script>
import {_Widget, Downloader} from "../common";

export default {
  name: 'myHeader',
  data: () => ({
    hasData:false,
    pinSuccess:false,
    dialogTableVisible: false,
    dialogFormVisible: false,
    config: {
      hospitalName: 'xxx医院',
      hospitalCode: '30323',
      carCode: '01',
      carId: '01',
      baseURL: "http://49.233.184.121:9090/ylfwservice/ylfw.asmx/",
    },
    operator:'',
    operatorId:'',
    formLabelWidth: '120px',
    bleMac: '',
    isBluetooth:false,
    changePersion:''
  }),
  methods:{
    goto(url){
      this.$router.push(url)
    },
    logout(){
      localStorage.removeItem("operator");
      localStorage.removeItem("operatorId");
      this.goto("/Login")
      //this.goto("/Login3")
    },
    save(){
      localStorage.setItem("config",JSON.stringify(this.config));
      this.dialogFormVisible = false;
      var operator = localStorage.getItem("operator");
      var operatorId = localStorage.getItem("operatorId");
      if(!operator && !operatorId){
        this.goto("/Login");
      }
    },
    conntentBluetooth(){
      var thisObject = this;
      window.printer.checkIsConnect(function (status){
        if(status == 1){
          thisObject.$message({
            type: 'success',
            message: '蓝牙重新连接中!'
          });
          window.closeConnect(function (){
            window.printer.init()
          })
        }else{
          window.closeConnect(function (){
            window.printer.init()
          })
        }
      })
    },
    syncData(){
      var thisObject = this;
      var url = thisObject.config.baseURL + "of_yf_tj";
      url += "?hospitalCode=" + thisObject.config.hospitalCode;
      var waitCollection = localStorage.getItem("waitCollection");
      if(waitCollection){
        waitCollection = JSON.parse(waitCollection);
        for(let i = 0; i < waitCollection.length; i++){
          Downloader.loadXMLAsync(url + "&as_json=" + JSON.stringify(waitCollection[i]),function(e) {
            var xmldoc = e.response;
            var obj = {};
            var xml = _Widget.xml.jsonToxml(xmldoc);
            var json = _Widget.xml.xmlToJson(xml);
            var res = (JSON.parse(json.string["#text"]));
            if(i == (waitCollection.length - 1) && res.result == 1){
              thisObject.$message({
                message: '核交数据提交完成！',
                type: 'success'
              });
              localStorage.removeItem("waitCollection");
              document.dispatchEvent(window.waitCollectionUpdate);
            }
          },function (err) {

          })
        }
      }
    }
  },
  mounted() {
    var thisObject = this;
    var localConfig = JSON.parse(localStorage.getItem("config")) ;
    var operator = localStorage.getItem("operator");
    var operatorId = localStorage.getItem("operatorId");
    this.isBluetooth = localStorage.getItem("isBluetooth");
    this.changePersion = localStorage.getItem("changePersion") || '';
    if(!localConfig){
      this.dialogFormVisible = true;
      return
    }
    if(localConfig && !operator && !operatorId){
      this.goto("/Login");
    }else{
      this.operator = operator;
      this.operatorId = operatorId;
      this.config = localConfig
    };
    //首次监听到
    document.addEventListener("bluetoothOn",function (e){
      this.isBluetooth = true;
    });
    document.addEventListener("bluetoothOff",function (e){
      this.isBluetooth = false;
      thisObject.$message({
        type: 'error',
        message: '蓝牙连接失败，请断开蓝牙，重新连接蓝牙!'
      });
    })
    document.addEventListener("scanDeviceSuccess",function (e){
      thisObject.$message({
        type: 'success',
        message: '蓝牙FAYA连接成功!'
      });
    })
    document.addEventListener("contentSuccess",function (e){
      thisObject.$message({
        type: 'success',
        message: '蓝牙称连接成功!'
      });
    })
    window.changePersion = document.createEvent("HTMLEvents");
    window.changePersion.initEvent("changePersion", false, false);
    document.addEventListener("changePersion",function (e){
      var changePersion = localStorage.getItem("changePersion");
      thisObject.changePersion = changePersion;
    })
    document.addEventListener("waitCollectionUpdate",function (e){
      var waitCollection = localStorage.getItem("waitCollection");
      if(waitCollection){
        thisObject.hasData = true;
      }else{
        thisObject.hasData = false;
      }
    });
    /*if(!this.pinSuccess){
      this.startScan(function (){
        thisObject.connectFAYA()
      });
    }*/
  }
};
</script>



