
var commonConfig = {
  MaxWeightErr:0.1
};
var _Widget = {};
_Widget.xml = (function (){
  var xml = {
    xmlToJson: function(xml, key) {
      var childNodes, result;
      if(key) {
        var keys = key.split("->");
        for(var i = 0; i < keys.length; i++) {
          if(i == 0) {
            childNodes = xml.getElementsByTagName(keys[i])[0];
          } else {
            childNodes = childNodes.getElementsByTagName(keys[i])[0]
          }
        }
      } else {
        childNodes = xml;
      }
      xml = childNodes;
      return parse(xml);

      function parse(xml) {
        // Create the return object
        var obj = {};
        if(xml.nodeType == 1) { // element
          // do attributes
          if(xml.attributes.length > 0) {
            obj["@attributes"] = {};
            for(var j = 0; j < xml.attributes.length; j++) {
              var attribute = xml.attributes.item(j);
              obj["@attributes"][attribute.nodeName] = attribute.nodeValue;
            }
          }
        } else if(xml.nodeType == 3) { // text
          obj = xml.nodeValue;
        }
        // do children
        if(xml.hasChildNodes()) {
          for(var i = 0; i < xml.childNodes.length; i++) {
            var item = xml.childNodes.item(i);
            var nodeName = item.nodeName;
            if(typeof(obj[nodeName]) == "undefined") {
              obj[nodeName] = parse(item);
            } else {
              if(typeof(obj[nodeName].length) == "undefined") {
                var old = obj[nodeName];
                obj[nodeName] = [];
                obj[nodeName].push(old);
              }
              obj[nodeName].push(parse(item));
            }
          }
        }
        return obj;
      }
    },
    jsonToxml: function(json){
      if(document.all){
        var xmlDom = new ActiveXObject("Microsoft.XMLDOM");
        xmlDom.loadXML(json);
        return xmlDom;
      }else{
        return new DOMParser().parseFromString(json, "text/xml");
      }
    }
  };
  return xml;
})();
_Widget.dateUtil = (function() {
  var tm = null;
  var dateUtil = {
    getThisMonth: function(sp) { //月日
      tm = new Date();
      var month = tm.getMonth() + 1;
      if((month + '').length == 1) {
        month = '0' + month;
      }
      var day = tm.getDate();
      if((day + '').length == 1) {
        day = '0' + day;
      }
      sp = sp == undefined ? '-' : sp;

      if(sp.indexOf(",") != -1) {
        return(month + sp.split(",")[0] + day + sp.split(",")[1]);
      } else {
        return(month + sp + day);
      }
    },
    getThisTime: function(sp) { //时分
      tm = new Date();
      var hours = tm.getHours();
      if((hours + '').length == 1) {
        hours = '0' + hours;
      }
      var minutes = tm.getMinutes();
      if((minutes + '').length == 1) {
        minutes = '0' + minutes;
      }
      sp = sp == undefined ? ':' : sp;
      return(hours + sp + minutes);
    },
    getThisWeek: function(num, weekDayName) { //周
      tm = new Date();
      if(num == undefined) {
        num = tm.getDay();
      }
      var _num = num >= 7 ? num % 7 : num;
      weekDayName = weekDayName || "星期";
      var CN_name = "日一二三四五六".charAt(_num);
      return weekDayName + CN_name;
    },
    formatDateTime: function (date) {
      var y = date.getFullYear();
      var m = date.getMonth() + 1;
      m = m < 10 ? ('0' + m) : m;
      var d = date.getDate();
      d = d < 10 ? ('0' + d) : d;
      var h = date.getHours();
      h=h < 10 ? ('0' + h) : h;
      var minute = date.getMinutes();
      minute = minute < 10 ? ('0' + minute) : minute;
      var second=date.getSeconds();
      second=second < 10 ? ('0' + second) : second;
      return y + '-' + m + '-' + d+' '+h+':'+minute+':'+second;
    },
    formatDateTime2: function (time, format) {//时间戳转日期格式
      var t = new Date(time);
      var tf = function(i){return (i < 10 ? '0' : '') + i};
      return format.replace(/yyyy|MM|dd|HH|mm|ss/g, function(a){
        switch(a){
          case 'yyyy':
            return tf(t.getFullYear());
            break;
          case 'MM':
            return tf(t.getMonth() + 1);
            break;
          case 'mm':
            return tf(t.getMinutes());
            break;
          case 'dd':
            return tf(t.getDate());
            break;
          case 'HH':
            return tf(t.getHours());
            break;
          case 'ss':
            return tf(t.getSeconds());
            break;
        }
      })
    }
  };
  return dateUtil;
})();
_Widget.getCode = function (action,type,carNo,time){
  //前两位 采集： 01  其他02
  //3位 类型 1：感染性 2：损伤性 3：病理性 4：化学性 5：药物性
  //4-5位 车号 01...
  //时间
  var t = _Widget.dateUtil.formatDateTime2(time,'yyyyMMddHHmmss');
  return action + type + carNo + t;
};
var Downloader = {};
Downloader.request = function(url,isAsync,onSuccess,onError,type,data,ContentType){
  var xmlHttp = GetXmlHttpObject();
  if (xmlHttp == null) {
    alert("您的浏览器不支持AJAX！");
    return;
  }
  xmlHttp.onreadystatechange = function() {
    if (xmlHttp.readyState == 4) {
      // var responseText = xmlHttp.responseText;
      // var result = eval('(' + responseText + ')');
      if(xmlHttp.status == 200 || xmlHttp.status == 304){
        onSuccess(xmlHttp);
      }else{
        if(typeof onError == "function"){
          onError(xmlHttp);
        }
      }
    }
  };
  try{
    xmlHttp.open(type, url, isAsync?true:false);//同步方式请求
    //xmlHttp.open("GET", url, true);第三个参数为true为异步方式
    if(ContentType){
      xmlHttp.setRequestHeader("Content-Type", ContentType)
    }

    xmlHttp.send(data);
  }catch (e){
    if(typeof onError == "function"){
      onError();
    }
  }
  function GetXmlHttpObject() {
    var xmlHttp = null;
    try {
      // Firefox, Opera 8.0+, Safari
      xmlHttp = new XMLHttpRequest();
    } catch (e) {
      // Internet Explorer
      try {
        xmlHttp = new ActiveXObject("Msxml2.XMLHTTP");
      } catch (e) {
        xmlHttp = new ActiveXObject("Microsoft.XMLHTTP");
      }
    }
    return xmlHttp;
  }
};

Downloader.loadXMLSync = function(url,onSuccess,onError){
  Downloader.request(url,false,onSuccess,onError,"GET",null);
};
Downloader.loadXMLAsync = function(url,onSuccess,onError){
  Downloader.request(url,true,onSuccess,onError,"GET",null);
};
Downloader.loadXMLAsyncPOST = function(url,data,onSuccess,onError){
  Downloader.request(url,true,onSuccess,onError,"POST",data, "application/x-www-form-urlencoded");
};
exports._Widget = _Widget;
exports.Downloader = Downloader;
exports.commonConfig = commonConfig;



