<template>
  <footer class="footer">
    <ul>
      <li v-for="nav in navs" :class="{on:$route.name==nav.name}" v-on:click="goto(nav.href)">{{nav.name}}</li>
    </ul>
  </footer>
</template>
<script>
export default {
  name: 'myFooter',

  data:() => ({
    isSelect:'/',
    navs:[{
      href:"/",
      name:"首页"
    },{
      href:"/updata",
      name:"核交"
    },{
      href:"/intoRoom",
      name:"入库"
    },{
      href:"/outRoom",
      name:"出库"
    },{
      href:"/error",
      name:"异常"
    }]
  }),
  methods:{
    goto(url){
      this.$router.push(url)
    }
  },
  created: function () {
    this.isSelect = this.$route.path;
  }
};
</script>





