<template>
  <div>
    <my-header></my-header>
    <el-main>
      <div id="errorPage" class="clearFixed">
        <ul class="errType clearFixed">
          <li v-for="(key,index) in errType" v-on:click="selType(index,key.errtype)"  v-bind:class="{on:seltype==index}">
            {{ key.name }}
          </li>
        </ul>
        <el-card class="errList">
          <el-table v-loading="loading" :data="tableData" style="width: 100%;height:460px">
            <el-table-column prop="code" label="标签条码" width="240"></el-table-column>
            <el-table-column prop="ksmc" label="污秽间" width="240"></el-table-column>
            <el-table-column prop="typeName" label="类型" width="120"></el-table-column>
            <el-table-column prop="weight" label="重量" width="120"></el-table-column>
            <el-table-column prop="ycbz" label="异常分类" width="120"></el-table-column>
            <el-table-column prop="time" label="异常时间" width="180"></el-table-column>
            <el-table-column fixed="right" label="操作">
              <template slot-scope="scope">
                <el-button @click="handleClick(scope.row)" type="text" size="small">异常处理</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-card>
      </div>
      <el-dialog title="异常原因" :visible.sync="dialogFormVisible">
        <el-form :model="form">
          <el-form-item label="异常原因" :label-width="formLabelWidth">
            <el-select v-model="form.ActiveErrtype" placeholder="请选择异常原因">
              <el-option v-for="(errs,index) in errorType" :key="index" :value="errs.iserror"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="save()">确 定</el-button>
        </div>
      </el-dialog>
    </el-main>
    <my-footer :active="active"></my-footer>
  </div>
</template>

<script>
import myHeader from './header';
import myFooter from './footer';
import {_Widget, Downloader} from '../common.js';
export default {
  name: 'error',
  components:{
    myHeader,
    myFooter,
  },
  data: () => ({
    active:'/error',
    loading: false,//数据接口完成后打开
    seltype:1,//选中序列默认值
    selErrtype:1,//选中的类型
    errType:[{
      name:"全部",
      errtype:0
    },{
      name:"未处理",
      errtype:1
    },{
      name:"已处理",
      errtype:2
    }],

    tableData: [],
    dialogTableVisible: false,
    dialogFormVisible: false,
    formLabelWidth: '120px',
    form: {
      ActiveErrtype: '',
    },
    errorType:[{
      iserror:"漏夜"
    },{
      iserror:"破损"
    },{
      iserror:"丢失"
    },{
      iserror:"未知"
    }],
    tempTableData:[],
    operator:"",
    operatorId:null,
  }),
  methods: {
    selType: function (index,errtype) {
      this.seltype = index;
      this.selErrtype = errtype;
      this.getData(this.selErrtype)
    },
    goto(url){
      this.$router.push(url)
    },
    handleClick(res){
      console.log(res);
      this.tempTableData = res;
      this.dialogFormVisible = true
    },
    getData(seltype){
      var thisObject = this;
      var url =  this.config.baseURL + "of_yc_mx";
      url += "?hospitalCode=" + thisObject.config.hospitalCode;
      Downloader.loadXMLAsync(url,function(e){
        var xmldoc = e.response;
        var obj = {};
        var xml = _Widget.xml.jsonToxml(xmldoc);
        var json = _Widget.xml.xmlToJson(xml);
        var res = (JSON.parse(json.string["#text"]));
        if(res.result == 1){
          //1已处理 0未处理 undefiend 全部
          var temp = [];
          res.data.forEach(function (item){
            switch (item.type2){
              case "1":
                item.typeName = "感染性";
                break;
              case "2":
                item.typeName = "损伤性";
                break;
              case "3":
                item.typeName = "病理性";
                break;
              case "4":
                item.typeName = "化学性";
                break;
              case "5":
                item.typeName = "药物性";
                break;
              default:
            }
            if(seltype){
              if(item.yccl_bz == seltype){
                temp.push(item)
              }
            }else{
              temp.push(item)
            }
          });
          thisObject.tableData = temp;
        }else{
          thisObject.$message({
            type: 'error',
            message: '没有异常数据!'
          });
        }
      },function (err) {
        thisObject.$message({
          type: 'error',
          message: '网络链接失败!'
        });
      });
    },
    getErrorType(){
      var thisObject = this;
      var url =  this.config.baseURL + "of_iserror";
      url += "?hospitalCode=" + thisObject.config.hospitalCode;
      Downloader.loadXMLAsync(url,function(e){
        var xmldoc = e.response;
        var obj = {};
        var xml = _Widget.xml.jsonToxml(xmldoc);
        var json = _Widget.xml.xmlToJson(xml);
        var res = (JSON.parse(json.string["#text"]));
        if(res.result == 1){
          thisObject.errorType = res.iserror;
        }
      },function (err) {
        thisObject.$message({
          type: 'error',
          message: '网络链接失败!'
        });
      });
    },
    save(){
      var thisObject = this;
      if(!thisObject.form.ActiveErrtype) {
        thisObject.$message({
          type: 'error',
          message: '请选择异常原因!'
        });
        return;
      };
      var time = new Date().getTime();
      var t = _Widget.dateUtil.formatDateTime2(time,'yyyy-MM-dd HH:mm:ss');
      var url = this.config.baseURL + "of_yf_tj";
      url += "?hospitalCode=" + thisObject.config.hospitalCode;
      /*var tempData = {
        type: "05",
        id: thisObject.operatorId,
        time:t,
        data:thisObject.tempTableData,
        errType:thisObject.form.ActiveErrtype
      };*/
      thisObject.tempTableData.id  = thisObject.operatorId;
      thisObject.tempTableData.t  = t;
      thisObject.tempTableData.errType  = thisObject.form.ActiveErrtype;
      thisObject.tempTableData.type  = "05";

      Downloader.loadXMLAsync(url + "&as_json=" + JSON.stringify(thisObject.tempTableData),function(e){
        var xmldoc = e.response;
        var obj = {};
        var xml = _Widget.xml.jsonToxml(xmldoc);
        var json = _Widget.xml.xmlToJson(xml);
        var res = (JSON.parse(json.string["#text"]));
        if(res.result == 1){
          thisObject.tempTableData = [];
          thisObject.dialogFormVisible = false;
          thisObject.$message({
            type: 'success',
            message: '提交成功!'
          });
          thisObject.getData(thisObject.selErrtype);
        }
      },function (err) {
        thisObject.$message({
          type: 'error',
          message: '网络链接失败!'
        });
      });
    }
  },
  mounted(){
    this.config = JSON.parse(localStorage.getItem("config"));
    this.operator = localStorage.getItem("operator");
    this.operatorId = localStorage.getItem("operatorId");
    this.getErrorType();
    this.getData();
  }
}
</script>



