<template>
  <div>
    <my-header></my-header>
    <el-main>
      <div id="intoRoomPage" class="clearFixed">
        <div class="hj hj1">
          <div class="title clearFixed">
            <span>待入库总数</span>
            <el-button  @click="addErr">异常入库</el-button>
            <!--<el-button>查看详情</el-button>-->
            <!--<el-button>打印入库码</el-button>-->
          </div>
          <div class="con">
            <div>
              <el-input v-model="form.updata.dai" placeholder="0" disabled></el-input>
              <span>袋</span>
            </div>
            <div>
              <el-input v-model="form.updata.kg" placeholder="0" disabled></el-input>
              <span>KG</span>
            </div>
          </div>
          <div class="detail">
            <el-table v-loading="loading" :data="tableData" style="width: 100%;max-height:473px">
              <el-table-column prop="code" label="采集编码" width="200"></el-table-column>
              <el-table-column prop="ksmc" label="科室" width="150"></el-table-column>
              <el-table-column prop="typeName" label="废物类型" width="150"></el-table-column>
              <el-table-column prop="weight" label="重量"></el-table-column>
            </el-table>
          </div>
        </div>
        <div class="hj hj2">
          <div class="title clearFixed">
            <span>已入库总数</span>
            <button id="btn_submit" @click="submits" >提交</button>
          </div>
          <div class="con">
            <div>
              <el-input v-model="form.updata1.dai" placeholder="0" disabled></el-input>
              <span>袋</span>
            </div>
            <div>
              <el-input v-model="form.updata1.kg" placeholder="0" disabled></el-input>
              <span>KG</span>
            </div>
          </div>
          <div class="detail">
            <el-form :inline="true" :model="input" class="demo-form-inline">
              <el-form-item label="入库条码">
                <el-input v-model="input.BoxCode" placeholder="条码" ></el-input>
              </el-form-item>
              <el-form-item label="采集条码">
                <el-input v-model="input.code"  placeholder="条码" ></el-input>
              </el-form-item>
            </el-form>
            <el-table v-loading="loading" :data="tempTableData" style="width: 100%;max-height:347px">
              <el-table-column prop="code" label="采集编码" width="200"></el-table-column>
              <el-table-column prop="ksmc" label="科室" width="150"></el-table-column>
              <el-table-column prop="typeName" label="废物类型" width="150"></el-table-column>
              <el-table-column prop="weight" label="重量"></el-table-column>
            </el-table>
          </div>
        </div>

      </div>
    </el-main>
    <my-footer></my-footer>
  </div>
</template>

<script>
import myHeader from './header';
import myFooter from './footer';
import {_Widget, Downloader} from "../common";
export default {
  name: 'intoRoom',
  components:{
    myHeader,
    myFooter,
  },
  data: () => ({
    loading:false,
    operator:"",
    operatorId:null,//操作员id
    input:{
      BoxCode:"",
      code:"",
    },
    boxType:null,//记录第一次扫箱子码时的type,防止再次录入废物类型不同
    form:{
      updata:{
        dai:0,
        kg:0
      },
      updata1:{
        dai:0,
        kg:0
      }
    },
    tableData: [],
    tempTableData:[],
    errAll:false
  }),
  methods: {
    getData(seltype){
      var thisObject = this;
      var url =  this.config.baseURL + "of_wrk_mx";
      url += "?hospitalCode=" + thisObject.config.hospitalCode;
      Downloader.loadXMLAsync(url,function(e){
        var xmldoc = e.response;
        var obj = {};
        var xml = _Widget.xml.jsonToxml(xmldoc);
        var json = _Widget.xml.xmlToJson(xml);
        var res = (JSON.parse(json.string["#text"]));
        if(res.result == 1){
          //1已处理 0未处理 undefiend 全部
          thisObject.form.updata.dai = res.zsl;
          thisObject.form.updata.kg = res.zzl;
          var temp = [];
          res.data.forEach(function (item){
            switch (item.type){
              case 1:
                item.typeName = "感染性";
                break;
              case 2:
                item.typeName = "损伤性";
                break;
              case 3:
                item.typeName = "病理性";
                break;
              case 4:
                item.typeName = "化学性";
                break;
              case 5:
                item.typeName = "药物性";
                break;
              default:
            }
            if(seltype){
              if(item.yccl_bz == seltype){
                temp.push(item)
              }
            }else{
              temp.push(item)
            }
          });
          thisObject.tableData = temp;
        }
      },function (err) {
        thisObject.$message({
          type: 'error',
          message: '网络链接失败!'
        });
      });
    },
    inputCode(code){
      var thisObject = this;

      if(!thisObject.input.BoxCode){
        this.$message({
          type: 'error',
          message: '请扫描入库条码!'
        });
        return;
      }
      thisObject.tableData.forEach(function (v,index){
        if(v.code == code){
          if(!thisObject.boxType){
            thisObject.boxType = v.fzh;
            thisObject.tempTableData.push(v);
            thisObject.tableData.splice(index,1);
            --thisObject.form.updata.dai;
            ++thisObject.form.updata1.dai;

            thisObject.form.updata.kg =  Number((thisObject.form.updata.kg - Number(parseFloat(v.weight).toFixed(2))).toFixed(2));
            thisObject.form.updata1.kg = Number((thisObject.form.updata1.kg + Number(parseFloat(v.weight).toFixed(2))).toFixed(2));
            //thisObject.input.BoxCode = "";
            thisObject.input.code = "";
          }else{
            if(thisObject.boxType == v.fzh){
              thisObject.tempTableData.push(v);
              thisObject.tableData.splice(index,1);
              --thisObject.form.updata.dai;
              ++thisObject.form.updata1.dai;
              thisObject.form.updata.kg =  Number((thisObject.form.updata.kg - Number(parseFloat(v.weight).toFixed(2))).toFixed(2));
              thisObject.form.updata1.kg = Number((thisObject.form.updata1.kg + Number(parseFloat(v.weight).toFixed(2))).toFixed(2));
              //thisObject.input.BoxCode = "";
              thisObject.input.code = "";
            }else{
              thisObject.$message({
                type: 'error',
                message: '该废物类型和已入库废物类型不符!'
              });
            }
          }

        }
      })
    },
    submits(evt){
      var thisObject = this;
      setTimeout(function (){
        let target = evt.target;
        if(target.nodeName == "SPAN"){
          target = evt.target.parentNode;
        }
        target.blur();
        document.body.click();
      },500)
      if(this.tempTableData.length == 0){
        this.$message({
          type: 'error',
          message: '请扫描待出库条码!'
        });
        return;
      };
      var url = thisObject.config.baseURL + "of_yf_tj";
      url += "?hospitalCode=" + thisObject.config.hospitalCode;
      var time = new Date().getTime();
      var t = _Widget.dateUtil.formatDateTime2(time,'yyyy-MM-dd HH:mm:ss');
      var tempData = {
        type: "03",
        id: thisObject.operatorId,
        time:t,
        codeBox:thisObject.input.BoxCode,
        data:thisObject.tempTableData
      };
      if(thisObject.errAll){
        tempData.isError = 2;
        tempData.data.forEach(function (item){
          item.isError = 2

        })
      }
      if(tempData.data.length){
        for(let i = 0; i < tempData.data.length; i++){
          var tdata = {
            type: "03",
            id: thisObject.operatorId,
            time:t,
            codeBox:thisObject.input.BoxCode,
            data:[tempData.data[i]]
          };
          Downloader.loadXMLAsync(url + "&as_json=" + JSON.stringify(tdata),function(e){
            var xmldoc = e.response;
            var obj = {};
            var xml = _Widget.xml.jsonToxml(xmldoc);
            var json = _Widget.xml.xmlToJson(xml);
            var res = (JSON.parse(json.string["#text"]));
            if(res.result == 1 && i == tempData.data.length -1){
              thisObject.$message({
                type: 'success',
                message: '入库成功!'
              });
              thisObject.tempTableData = [];
              thisObject.form.updata1.dai = 0;
              thisObject.form.updata1.kg = 0;
              thisObject.input.BoxCode = "";
              thisObject.input.code = "";
              thisObject.boxType = '';
            }
          },function (err) {
            thisObject.$message({
              type: 'error',
              message: '网络链接失败!'
            });
          });
        }
      }

    },
    onkeydown(e){
      var thisObject = this;
      //模拟：
      //thisObject.keyval = thisObject.input.code;
      if(e.keyCode == 13){
        if(thisObject.keyval.length != 19){//入库条码
          thisObject.input.BoxCode = thisObject.keyval;
          this.keyval = '';
        }
        if(thisObject.keyval.length == 19){
          thisObject.input.code = thisObject.keyval;
          thisObject.inputCode(thisObject.input.code)
          this.keyval = '';
        }
        return
      }
      thisObject.keyval += String.fromCharCode(e.keyCode);
    },
    addErr(){
      this.tempTableData = this.tableData;
      this.tableData = [];
      this.form.updata1.dai = this.form.updata.dai;
      this.form.updata1.kg = this.form.updata.kg;
      this.form.updata.dai = 0;
      this.form.updata.kg = 0;
      this.errAll = true;
    },
    /*打印入库码
    * 条码信息：入库出库类型+废物类型+箱号
    * */
  },
  mounted(){
    this.config = JSON.parse(localStorage.getItem("config"));
    this.operator = localStorage.getItem("operator");
    this.operatorId = localStorage.getItem("operatorId");
    this.getData();
    this.keyval = "";
    document.addEventListener("keydown",this.onkeydown);
  },
  destroyed(){
    this.keyval = "";
    document.removeEventListener("keydown",this.onkeydown)
  }
}
</script>



