<template>
  <div id="settings" class="scope-test" style="width: 50%;margin: 10px auto 0;">
    <el-form :model="config">
      <el-form-item label="医院名称" :label-width="formLabelWidth">
        <el-input v-model="config.hospitalName" autocomplete="off" data-mode="en_cap"></el-input>
      </el-form-item>
      <el-form-item label="医院编码" :label-width="formLabelWidth">
        <el-input v-model="config.hospitalCode" autocomplete="off" data-mode="num"></el-input>
      </el-form-item>
      <el-form-item label="车辆编号" :label-width="formLabelWidth">
        <el-input v-model="config.carCode" autocomplete="off" data-mode="num"></el-input>
      </el-form-item>
      <el-form-item label="车辆ID" :label-width="formLabelWidth">
        <el-input v-model="config.carId" autocomplete="off" data-mode="num"></el-input>
      </el-form-item>
      <el-form-item label="接口地址" :label-width="formLabelWidth" >
        <el-input v-model="config.baseURL" autocomplete="off" data-mode="en_cap"></el-input>
      </el-form-item>
      <el-form-item label="车体重量" :label-width="formLabelWidth">
        <el-input v-model="config.carWeight" autocomplete="off" data-mode="num"></el-input>
      </el-form-item>
      <el-form-item label="可误差重量" :label-width="formLabelWidth">
        <el-input v-model="config.diffWeight" autocomplete="off" data-mode="num"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer" style="text-align: center;">
      <el-button style="width: 30%;margin: 0 10px;" @click="goto('/')">取 消</el-button>
      <el-button style="width: 30%;margin: 0 10px;" type="primary" @click="save()">确 定</el-button>
    </div>
    <vue-dusion-keyboard ref="zzz" scope=".scope-test" @keyvalue="kkk" @del="del" style="left:0px;width: 100%;"></vue-dusion-keyboard>
  </div>
</template>

<script>
import {VueDusionKeyboard,Paint} from 'vue-dusion-keyboard'
export default {
  name: 'settings',
  components:{
    VueDusionKeyboard,
    Paint
  },
  data: () => ({
    config: {
      hospitalName: 'xxx医院',
      hospitalCode: '30323',
      carCode: '01',
      carId: '01',
      baseURL: "http://49.233.184.121:9090/ylfwservice/ylfw.asmx/",
      carWeight:50,
      diffWeight:0.1
    },
    operator:'',
    operatorId:'',
    formLabelWidth: '120px'
  }),
  methods:{
    result(res) {
      console.log(res);
    },
    kkk(kk) {
      console.log(kk);
    },
    del(text, index) {
      console.log(text);
      console.log(index);
    },
    goto(url){
      this.$router.push(url)
    },
    logout(){
      localStorage.removeItem("operator");
      localStorage.removeItem("operatorId");
      this.goto("/Login")
    },
    save(){
      localStorage.setItem("config",JSON.stringify(this.config));
      this.dialogFormVisible = false;
      var operator = localStorage.getItem("operator");
      var operatorId = localStorage.getItem("operatorId");
      if(!operator && !operatorId){
        this.goto("/Login");
      }else{
        this.goto("/");
      }
    }
  },
  mounted() {
    var localConfig = JSON.parse(localStorage.getItem("config")) ;
    var operator = localStorage.getItem("operator");
    var operatorId = localStorage.getItem("operatorId");
    if(!localConfig){
      this.dialogFormVisible = true;
      return
    }
    if(localConfig && !operator && !operatorId){
      this.goto("/Login");
    }else{
      this.operator = operator;
      this.operatorId = operatorId;
      this.config = localConfig;
    }

  }
};
</script>



