<template>
  <div>
    <my-header></my-header>
    <el-main>
      <div id="collectionListPage" class="clearFixed">
        <el-card class="errList">
          <el-table v-loading="loading" :data="tableData" style="width: 100%;height:460px">
            <el-table-column prop="code" label="标签条码" width="180"></el-table-column>
            <el-table-column prop="deptname" label="污秽间" width="180"></el-table-column>
            <el-table-column prop="typeName" label="类型" width="180"></el-table-column>
            <el-table-column prop="weight" label="重量" width="180"></el-table-column>
            <el-table-column prop="time" label="时间" width="180"></el-table-column>
            <el-table-column prop="person" label="采集人" width="180"></el-table-column>
            <el-table-column fixed="right" label="操作">
              <template slot-scope="scope">
                <el-button v-on:click="print(scope.row)" type="text" size="small">打印</el-button>
                <el-button v-on:click="del(scope.row,scope.$index)" type="text" size="small">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-card>
      </div>
    </el-main>
    <my-footer :active="active"></my-footer>
  </div>
</template>

<script>
import myHeader from './header';
import myFooter from './footer';
import {_Widget} from "../common";
import {Downloader} from "../common";
import {printLable} from '../../static/mmprinter.js'
export default {
  name: 'collectionList',
  components:{
    myHeader,
    myFooter,
  },
  data: () => ({
    active:'/error',
    car: '',
    doctor:'',
    loading: false,//数据接口完成后打开
    seltype:0,//选中序列默认值
    selErrtype:0,//选中的类型
    tableData: [],

  }),
  methods: {
    printLable,
    selType: function (index,errtype) {
      this.seltype = index;
      this.selErrtype = errtype;
      this.getData(this.selErrtype)
    },
    goto(url){
      this.$router.push(url)
    },
    handleClick(res){

    },
    getdeptData(){
      var thisObject = this;
      var url = thisObject.config.baseURL + "of_cj_sy";
      url += "?hospitalCode=" + thisObject.config.hospitalCode + "&depcode=" + thisObject.deptid;
      Downloader.loadXMLAsync(url, function(e) {
        var xmldoc = e.response;
        var obj = {};
        var xml = _Widget.xml.jsonToxml(xmldoc);
        var json = _Widget.xml.xmlToJson(xml);
        var res = (JSON.parse(json.string["#text"]));
        if(res.result == 1){
          res.data.forEach(function (v){
            switch (v.type){
              case 1:
                v.typeName = "感染性";
                break;
              case 2:
                v.typeName = "损伤性";
                break;
              case 3:
                v.typeName = "病理性";
                break;
              case 4:
                v.typeName = "化学性";
                break;
              case 5:
                v.typeName = "药物性";
                break;
              default:
            }
          })
          thisObject.tableData = res.data;
        }
      },function (err) {
        thisObject.$message({
          type: 'error',
          message: '网络链接失败!'
        });
        thisObject.tableData =[];
      })
    },
    print(data){
      var params = {
        deptname: "科室：" + data.deptname,
        time: "时间：" + data.time,
        type: data.typeName,
        weight: "重量：" + data.weight,
        hospital: "医院：" + this.config.hospitalName,
        code: data.code,
        person: '采集人：' + data.person
      };
      this.printLable(params)
    },
    del(data,index){
      var thisObject = this;
      this.$confirm('此操作将删除该记录, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        var time = new Date().getTime();
        var t = _Widget.dateUtil.formatDateTime2(time,'yyyy-MM-dd HH:mm:ss');
        var id = thisObject.operatorId;//操作员
        var url = thisObject.config.baseURL + "of_yf_tj";
        url += "?hospitalCode=" + thisObject.config.hospitalCode;
        var linshiData = {
          type: "06",
          code: data.code,
          id:id,
          time:t
        };
        Downloader.loadXMLAsync(url + "&as_json=" + JSON.stringify(linshiData),function(e){
          var xmldoc = e.response;
          var xml = _Widget.xml.jsonToxml(xmldoc);
          var json = _Widget.xml.xmlToJson(xml);
          var res = (JSON.parse(json.string["#text"]));
          if(res.result == 1){
            thisObject.tableData.splice(index, 1);
            thisObject.$message({
              type: 'success',
              message: '删除成功!'
            });
          }
        },function (err) {
          thisObject.$message({
            type: 'error',
            message: '网络链接失败!'
          });
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });

    }
  },
  mounted(){
    var thisObject = this;
    this.config = JSON.parse(localStorage.getItem("config"));
    this.operator = localStorage.getItem("operator");
    this.operatorId = localStorage.getItem("operatorId");
    this.deptid = this.$route.query.deptid;
    this.getdeptData();
  }
}
</script>



