import Vue from 'vue'
import Router from 'vue-router'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import index from '@/components/index'
import depCollection from '@/components/depCollection'
import updata from '@/components/updata'
import intoRoom from '@/components/intoRoom'
import outRoom from '@/components/outRoom'
import error from '@/components/error'
import collectionList from '@/components/collectionList'
import updataDetail from '@/components/updataDetail'
import Login from '@/components/Login'
import Login2 from '@/components/Login2'
import Login3 from '@/components/Login3'
import Register from '@/components/Register'
import numberKeyboard from '@/components/numberKeyboard'
import settings from '@/components/settings'
Vue.use(Router)
Vue.use(ElementUI);
export default new Router({
  routes: [
    {
      path: '/',
      name: '首页',
      component: index
    }, {
      path: '/depCollection',
      name: '科室收集',
      component: depCollection
    }, {
      path: '/updata',
      name: '核交',
      component: updata
    }, {
      path: '/intoRoom',
      name: '入库',
      component: intoRoom
    }, {
      path: '/outRoom',
      name: '出库',
      component: outRoom
    }, {
      path: '/error',
      name: '异常',
      component: error
    }, {
      path: '/collectionList',
      name: '收集列表',
      component: collectionList
    }, {
      path: '/updataDetail',
      name: '核交明细',
      component: updataDetail
    }, {
      path: '/Login',
      name: '登录',
      component: Login
    }, {
      path: '/Login2',
      name: '登录',
      component: Login2
    }, {
      path: '/Login3',
      name: '登录',
      component: Login3
    },{
      path: '/Register',
      name: '注册',
      component: Register
    }, {
      path: '/numberKeyboard',
      name: '键盘',
      component: numberKeyboard
    }, {
      path: '/settings',
      name: '设置',
      component: settings
    }
  ]
})



