<template>
  <div>
    <my-header></my-header>
    <el-main>
      <!--<div class="hello">
        <button @click="startScan"> 搜索蓝牙设备</button>
        <button @click="connectFAYA"> 链接蓝牙称</button>
        <button @click="readGW"> 毛重</button>
        <button @click="readNW"> 净重</button>
        <span>{{ NW }}</span>
        <button @click="readTW"> 皮重</button>
        <button @click="printLable"> 打印测试</button>
      </div>-->
      <div id="updataPage" class="clearFixed">
        <div class="hj hj1">
          <div class="title">
            待核交
            <el-button @click="addErr">异常核交</el-button>
            <el-button @click="addAll">整车核交</el-button>
            <!--<el-button @click="show1=!show1">查看明细</el-button>-->
          </div>
          <div class="con clearFixed">
            <!--<div>
               <el-input v-model="form.updata.he" placeholder="000" disabled></el-input>
               <span>盒</span>
             </div>-->
            <div>
              <el-input v-model="form.wait.dai" placeholder="0" disabled></el-input>
              <span>袋</span>
            </div>
            <div>
              <el-input v-model="form.wait.kg" placeholder="0" disabled></el-input>
              <span>KG</span>
            </div>
          </div>
          <div class="detail">
            <el-table v-loading="loading" :data="tableData" style="width: 100%;max-height:473px; overflow-y: scroll">
              <el-table-column prop="code" label="采集编码" width="150"></el-table-column>
              <el-table-column prop="ksmc" label="科室" width="80"></el-table-column>
              <el-table-column prop="typeName" label="废物类型" width="80"></el-table-column>
              <el-table-column prop="weight" label="重量"></el-table-column>
            </el-table>
          </div>
        </div>
        <div class="hj hj2" >
          <div class="title">
            已核交
            <el-button @click="submits('02')">核交</el-button>
          </div>
          <div class="con clearFixed">
            <div>
              <el-input v-model="form.updataed.dai" placeholder="0" disabled></el-input>
              <span>袋</span>
            </div>
            <div>
              <el-input v-model="form.updataed.kg" placeholder="0" disabled></el-input>
              <span>KG</span>
            </div>
          </div>
          <el-form :inline="true" :model="input" class="demo-form-inline putBox" @submit.native.prevent >
            <div class="inputLeft">
              <el-form-item class="clearFixed" label="条码">
                <!--<el-input v-model="input.code" placeholder="条码" @keyup.enter.native="keyEnter"></el-input>-->
                <el-input ref="btnCode" disabled="disabled" v-model="input.code" placeholder="条码"></el-input>
                <el-button type="success" round  @click="clear">清除</el-button>
              </el-form-item>
              <el-form-item class="clearFixed" label="重量">
                <el-input ref="btnWeight"  :disabled="!pushAll" v-model="input.weight" placeholder="重量" @focus="closeNumberKeyboard(1)" ></el-input>
                <!--<el-button type="primary" round  @click="getWeight">称重</el-button>-->
                <button class="btn_bule"  @click="getWeight">称重</button>
              </el-form-item>
            </div>
          </el-form>
          <div class="detail" >
            <el-table v-loading="loading"  :data="tempTableData" style="width: 100%;max-height:400px; overflow-y: scroll">
              <el-table-column prop="code" label="采集编码" width="150"></el-table-column>
              <el-table-column prop="ksmc" label="科室" width="80"></el-table-column>
              <el-table-column prop="type" label="废物类型" width="80"></el-table-column>
              <el-table-column prop="weight" label="重量"></el-table-column>
            </el-table>
          </div>

        </div>
        <div class="hj hj3">
          <div class="title">
            异常
            <el-button @click="submitErr">核交</el-button>
          </div>
          <div class="con">
            <div>
              <el-input v-model="form.error.dai" placeholder="0" disabled></el-input>
              <span>袋</span>
            </div>
            <div>
              <el-input v-model="form.error.kg" placeholder="0" disabled></el-input>
              <span>KG</span>
            </div>
          </div>
          <div class="detail">
            <el-table v-loading="loading" :data="tempErrData" style="width: 100%;max-height:473px; overflow-y: scroll">
              <el-table-column prop="code" label="采集编码" width="150"></el-table-column>
              <el-table-column prop="ksmc" label="科室" width="80"></el-table-column>
              <el-table-column prop="typeName" label="废物类型" width="80"></el-table-column>
              <el-table-column prop="weight" label="重量"></el-table-column>
            </el-table>
          </div>
        </div>
      </div>
      <el-dialog
        :title="addFlag?'新增':'修改'"
        style="text-align:left !important"
        :visible.sync="dialogVisible"
        :before-close="handleClose">
        <el-form ref="form" label-width="80px">
          <el-form-item label="重量" style="width:190px">
            <el-input ref="inputWeight" v-model="obj.weight" placeholder="请输入重量"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
        <el-button type="success" @click="save()">提交</el-button>
        <el-button type="primary" @click="dialogVisible = false">取消</el-button>
      </span>
      </el-dialog>
      <number-keyboard v-model="isShowAmountKeyboard" @confirm="closeNumberKeyboard(0)" @delete="input.weight ? (input.weight = input.weight.substring(0, input.weight.length - 1)) : ''" @keyDown="withdrawAmountInput"></number-keyboard>
    </el-main>
    <my-footer></my-footer>
  </div>
</template>

<script>
import myHeader from './header';
import myFooter from './footer';
import {_Widget} from "../common";
import {Downloader} from "../common";
import {commonConfig} from "../common";
import {printLable} from '../../static/mmprinter.window.js';
import numberKeyboard from './numberKeyboard';
import { Loading } from 'element-ui';
export default {
  name: 'updata',
  components:{
    myHeader,
    myFooter,
    numberKeyboard
  },
  data: () => ({
    isShowAmountKeyboard: false, // 是否展示键盘
    loading:false,
    dialogVisible: false,
    addFlag:false,
    show1:false,
    show2:false,
    show3:false,
    input:{
      code:"",
      weight:""
    },
    operatorId:null,//操作员ID
    carNo:3,
    obj:{
      weight:0
    },
    form:{
      wait:{
        dai:0,
        kg:0
      },
      updataed:{
        dai:0,
        kg:0
      },
      error:{
        dai:0,
        kg:0
      }
    },
    pushAll:false,//是否是整车核交
    errAll:false,//是否是异常核交
    tableData:[],
    tableData1: [],
    tableData2: [],
    tempTableData:[],
    tempErrData:[],
    bleMac: '',
    faya: {
      service_uuid: '49535343-FE7D-4AE5-8FA9-9FAFD205E455',
      characteristic_uuid_TX: '49535343-8841-43F4-A8D4-ECBE34729BB3',
      characteristic_uuid_RX: '49535343-1E4D-4BD9-BA61-23C647249616'
    },
    NW: ''
  }),
  methods: {
    printLable,
    loadingInstance(){
      return Loading.service({
        lock: false,
        text: '拼命加载中...',//可以自定义文字
        spinner:'el-icon-loading',//自定义加载图标类名
        background: 'rgba(0, 0, 0, 0.7)'//遮罩层背景色
      })
    } ,
    withdrawAmountInput(key){
      if(this.input.weight.indexOf(".") != -1) {
        if(key === ".") {
          console.error('格式错误!');
          return;
        } else if(this.input.weight.split(".")[1].length > 1) {
          console.error('最多只能输入2位小数!');
          return;
        }
      }
      this.input.weight += key;
    },
    // 关闭所有键盘弹窗
    closeNumberKeyboard(type){
      var thisObject = this;
      if(type == 1){
        this.isShowAmountKeyboard = true;
      }else{
        this.isShowAmountKeyboard = false;
        if(thisObject.input.weight){
          thisObject.scanCode();
        }
      }
    },
    handleClick(){
      console.log("handleClick");
    },
    handleClose(done) {
      done();
    },
    goto(url){
      this.$router.push(url);
    },
    submits(typecode){
      var thisObject = this;
      if(!thisObject.input.weight && thisObject.pushAll){
        thisObject.$message({
          type: 'error',
          message: '请输入重量!'
        });
        //整车核交
        if(thisObject.pushAll){
          thisObject.$refs.btnWeight.focus();
        }
        return;
      }
      thisObject.form.updataed.dai = parseInt(thisObject.form.updataed.dai);
      thisObject.form.updataed.kg = parseFloat(thisObject.form.updataed.kg);
      thisObject.input.weight = parseFloat(thisObject.input.weight);
      var time = new Date().getTime();
      var t = _Widget.dateUtil.formatDateTime2(time,'yyyy-MM-dd HH:mm:ss');
      var name = '';//操作员
      var url = this.config.baseURL + "of_yf_tj";
      url += "?hospitalCode=" + thisObject.config.hospitalCode;
      var linshiData = {
        type: typecode,
        id: thisObject.operatorId,
        time:t,
        data:thisObject.tempTableData,
        infactWeight:0
      };
      if(typecode == "05" || thisObject.errAll){
        linshiData.isError = 2;
        typecode = "02";
        linshiData.forEach(function (item){
          item.isError = 2
        })
      }
      /*var para = [{
        type:"02",//采集01 核交02 入库03 异常04
        id:thisObject.id,
        weight:this.input.weight,
        code:this.input.code,
        time:t
      }];*/
      //整车核交
      if(thisObject.pushAll){
        var infactWeight = thisObject.input.weight - parseFloat(thisObject.config.carWeight);
        var diff =  thisObject.form.updataed.dai * thisObject.config.diffWeight
        if(Math.abs(thisObject.form.updataed.kg - infactWeight) > diff){
          thisObject.$message({
            type: 'error',
            message: '整车重量超出误差上限'
          });
          this.tableData = this.tempTableData;
          this.tempTableData = [];
          this.form.wait.dai = this.form.updataed.dai;
          this.form.wait.kg = this.form.updataed.kg;
          this.form.updataed.dai = 0;
          this.form.updataed.kg = 0;
          this.pushAll = false;
          return
        }else{
          linshiData.infactWeight = Number(infactWeight.toFixed(2))
          this.pushAll = false;
        }
      }
      //thisObject.loadingInstance = Loading.service({ fullscreen: false });
      for(let i = 0; i < linshiData.data.length; i++){
        var tempLinshiData = {
          type: typecode,
          id: thisObject.operatorId,
          time:t,
          data:[linshiData.data[i]],
          infactWeight:infactWeight
        };
        Downloader.loadXMLAsync(url + "&as_json=" + JSON.stringify(tempLinshiData),function(e){
          var xmldoc = e.response;
          var obj = {};
          var xml = _Widget.xml.jsonToxml(xmldoc);
          var json = _Widget.xml.xmlToJson(xml);
          var res = (JSON.parse(json.string["#text"]));
          if(res.result == 1){
            //核交
            if(i == linshiData.data.length- 1){
              if(typecode == "02"){
                thisObject.tempTableData = [];
                thisObject.input.code = "";
                thisObject.input.weight = "";
                thisObject.$message({
                  type: 'success',
                  message: '核交成功!'
                });
              }
              if(typecode == "05"){
                thisObject.$message({
                  type: 'success',
                  message: '加入异常成功!'
                });
              }
            }
            //thisObject.loadingInstance.close();
            //强制刷新
            //thisObject.$forceUpdate();

          }
        },function (err) {
          thisObject.$message({
            type: 'error',
            message: '网络链接失败!'
          });
        })
      }

    },
    submitErr(){
      var thisObject = this;
      thisObject.form.error.dai = parseInt(thisObject.form.error.dai);
      thisObject.form.error.kg = parseFloat(thisObject.form.error.kg);
      var time = new Date().getTime();
      var t = _Widget.dateUtil.formatDateTime2(time,'yyyy-MM-dd HH:mm:ss');
      var name = '';//操作员
      var url = this.config.baseURL + "of_yf_tj";
      url += "?hospitalCode=" + thisObject.config.hospitalCode;
      var linshiData = {
        type: "02",
        id: thisObject.operatorId,
        time:t,
        data:thisObject.tempErrData,
        infactWeight:0,
        isError:2
      };
      for(let i = 0; i < linshiData.data.length; i++){
        linshiData.data[i].isError = 2;
        var tempLinshiData = {
          type: "02",
          id: thisObject.operatorId,
          time:t,
          data:[linshiData.data[i]],
          infactWeight:0,
          isError:2
        };
        //thisObject.loadingInstance = Loading.service({ fullscreen: false });
        Downloader.loadXMLAsync(url + "&as_json=" + JSON.stringify(tempLinshiData),function(e){
          var xmldoc = e.response;
          var obj = {};
          var xml = _Widget.xml.jsonToxml(xmldoc);
          var json = _Widget.xml.xmlToJson(xml);
          var res = (JSON.parse(json.string["#text"]));
          if(res.result == 1){
            if(i == linshiData.data.length- 1){
              thisObject.tempErrData = [];
              thisObject.form.error.dai = 0;
              thisObject.form.error.kg = 0;
              thisObject.$message({
                type: 'success',
                message: '加入异常成功!'
              });
            }
          }
        },function (err) {
          thisObject.$message({
            type: 'error',
            message: '网络链接失败!'
          });
        })
      }

    },
    save(){
      this.$message({
        message: '修改完成',
        type: 'success'
      });
      this.dialogVisible = false;
    },
    edit(row) {
      this.book = row;
      this.dialogVisible = true;
      this.addFlag = false;
    },
    addAll(){
      if(this.tableData.length){
        this.tempTableData = this.tableData;
        this.tableData = [];
        this.form.updataed.dai = this.form.wait.dai;
        this.form.updataed.kg = this.form.wait.kg;
        this.form.wait.dai = 0;
        this.form.wait.kg = 0;
        this.pushAll = true;
      }
    },
    addErr(){
      if(this.tableData.length){
        this.tempErrData = this.tableData;
        this.tableData = [];
        this.form.error.dai = this.form.wait.dai;
        this.form.error.kg = this.form.wait.kg;
        this.form.wait.dai = 0;
        this.form.wait.kg = 0;
        this.errAll = true;
      }
    },
    //待核交
    getData(){
      var thisObject = this;
      var time = new Date().getTime();
      var t = _Widget.dateUtil.formatDateTime2(time,'yyyy-MM-dd HH:mm:ss');
      var name = '';//操作员
      var url = this.config.baseURL + "of_whj_mx";
      url += "?hospitalCode=" + thisObject.config.hospitalCode;
      var para = {
        carId:this.config.carId
      }
      //thisObject.loadingInstance = Loading.service({ fullscreen: false });
      Downloader.loadXMLAsync(url + "&as_json=" + JSON.stringify(para),function(e){
        var xmldoc = e.response;
        var obj = {};
        var xml = _Widget.xml.jsonToxml(xmldoc);
        var json = _Widget.xml.xmlToJson(xml);
        var res = (JSON.parse(json.string["#text"]));
        if(res.result == 1){
          console.log(res);
          res.data = res.data.sort(function compareFunction(item1, item2) {
            return item1.ksmc.localeCompare(item2.ksmc);
          });
          res.data.forEach(function (item){
            switch (item.type){
              case 1:
                item.typeName = "感染性";
                break;
              case 2:
                item.typeName = "损伤性";
                break;
              case 3:
                item.typeName = "病理性";
                break;
              case 4:
                item.typeName = "化学性";
                break;
              case 5:
                item.typeName = "药物性";
                break;
              default:
            }
          });
          thisObject.tableData = res.data;
          thisObject.form.wait.dai = res.zsl;
          thisObject.form.wait.kg = res.zzl;
          //thisObject.loadingInstance.close();
        }
      },function (err) {
        thisObject.$message({
          type: 'error',
          message: '网络链接失败!'
        });
      })
    },
    //已核交
    /*getData1(){
      var thisObject = this;
      var url = this.config.baseURL + "of_wrk_mx";
      Downloader.loadXMLAsync(url,function(e){
        var xmldoc = e.response;
        var obj = {};
        var xml = _Widget.xml.jsonToxml(xmldoc);
        var json = _Widget.xml.xmlToJson(xml);
        var res = (JSON.parse(json.string["#text"]));
        if(res.result == 1){
          console.log(res);
          thisObject.tableData1 = res.data;
          thisObject.form.updataed.dai = res.zsl;
          thisObject.form.updataed.kg = res.zzl;
        }
      },function (err) {
        thisObject.$message({
          type: 'error',
          message: '网络链接失败!'
        });
      });
    },*/
    //异常
    getData2(){
      var thisObject = this;
      var url = this.config.baseURL + "of_yc_mx";
      url += "?hospitalCode=" + thisObject.config.hospitalCode;
      //thisObject.loadingInstance = Loading.service({ fullscreen: false });
      Downloader.loadXMLAsync(url,function(e){
        var xmldoc = e.response;
        var obj = {};
        var xml = _Widget.xml.jsonToxml(xmldoc);
        var json = _Widget.xml.xmlToJson(xml);
        var res = (JSON.parse(json.string["#text"]));
        if(res.result == 1){
          console.log(res);
          thisObject.tableData2 = res.data;
          thisObject.form.error.dai = res.zsl;
          thisObject.form.error.kg = res.zzl;
          //thisObject.loadingInstance.close();
        }
      },function (err) {
        thisObject.$message({
          type: 'error',
          message: '网络链接失败!'
        });
      });
    },
    scanCode(){
      //扫码时检查待核交数据，如果code一致，输入重量后，将数据加到已核交中
      var thisObject = this;
      var code = thisObject.input.code;
      var weight = thisObject.input.weight;
      if(!weight){
        return
      }
      thisObject.form.wait.dai = parseInt(thisObject.form.wait.dai);
      thisObject.form.wait.kg = parseFloat(thisObject.form.wait.kg);
      thisObject.form.updataed.dai = parseInt(thisObject.form.updataed.dai);
      thisObject.form.updataed.kg = parseFloat(thisObject.form.updataed.kg);
      var flag = 1;
      thisObject.tableData.forEach(function (item,i){
        if(code == item.code){
          flag = 2;

          item.weight = parseFloat(item.weight);
          if(Math.abs(weight - item.weight) < commonConfig.MaxWeightErr){
            --thisObject.form.wait.dai;
            ++thisObject.form.updataed.dai;
            thisObject.form.wait.kg = Number((thisObject.form.wait.kg - item.weight).toFixed(2));
            thisObject.form.updataed.kg = Number((thisObject.form.updataed.kg + item.weight).toFixed(2))
            thisObject.tempTableData.push(item);
            thisObject.tableData.splice(i,1);
            thisObject.input.code = '';
            thisObject.input.weight = 0;
          }else{
            /*thisObject.$confirm('重量误差超出限制，是否加入异常?', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {*/
            thisObject.$message({
              type: 'error',
              message: '重量误差超出限制，已加入异常'
            });
              thisObject.tableData.splice(i,1);
              thisObject.tempErrData.push(item);
              ++thisObject.form.error.dai;
              thisObject.form.error.weight = Number((thisObject.form.error.weight + parseFloat(item.weight)).toFixed(2));
            /*}).catch(() => {
              thisObject.$message({
                type: 'info',
                message: '已取消'
              });
            });*/
          }
        }
      })

      if(flag == 1 && !thisObject.pushAll){
        thisObject.$message({
          type: 'error',
          message: '条码错误!未在待审核中找到该条码！'
        });
      }
    },
    getWeight(evt){
      //模拟获取重量
      /*this.input.weight = 3.02;
      this.$refs.btnCode.focus();
      this.scanCode();
      return*/

      //打开蓝牙称，获取重量
      //this.$refs.btnCode.focus();
      setTimeout(function (){
        let target = evt.target;
        if(target.nodeName == "SPAN"){
          target = evt.target.parentNode;
        }
        target.blur();
        document.body.click();
      },500)
      var thisObject = this;
      function call(data){
        console.log(data);
      };
      function call2(data){
        console.log(data);
        thisObject.input.weight = window.NW;
        if(thisObject.input.weight){
          thisObject.scanCode();
        }
      };
      window.printer.readNW(call);
      setTimeout(function (){
        window.printer.readNW(call2);
      },1000);
    },
    keyEnter(obj){
      var thisObject = this;
      console.log("keyEnter");

      //扫描条码完成事件
      //this.getWeight()
    },
    clear(){
      console.log("clear()");
      this.input.code = '';
      //this.$refs.inputWeight.focus();
    },
    onkeydown2(e){
       var thisObject = this;
       if(e.keyCode == 13){
         console.log("keyCode==13 " + thisObject.keyval);
         thisObject.input.code = thisObject.keyval;
         thisObject.keyval = '';
         return
       }
       thisObject.keyval += String.fromCharCode(e.keyCode);
    }
  },
  mounted() {
    var thisObject = this;
    this.config = JSON.parse(localStorage.getItem("config"));
    this.operator = localStorage.getItem("operator");
    this.operatorId = localStorage.getItem("operatorId");
    this.getData();
    //this.getData1();
    //this.getData2();
    /*this.startScan(function (){
      thisObject.connectFAYA()
    });*/
    this.keyval = "";
    document.addEventListener("keydown",this.onkeydown2);
  },
  destroyed(){
    this.keyval = "";
    document.removeEventListener("keydown",this.onkeydown2)
  }
}
</script>



