<template>
  <div>
   <my-header></my-header>
    <el-main>
      <div id="depCollPage">
        <div class="box1 clearFixed">
          <div class="exit">
            <i class="iconfont icondenglu"></i>
            <div @click="goto('/')">返回</div>
          </div>
          <div class="dep">
            <div class="dpename">{{deptname}}</div>
            <div class="infos">
              <span><strong>{{ todayTotal_d1.zsl }}</strong> 袋</span>
              <span><strong>{{ todayTotal_d1.zzl }}</strong> KG</span>
            </div>
          </div>
          <div class="deatail">
            <div class="tabheader">
              <span class="code"><strong>标签条码</strong></span>
              <span class="type"><strong>类型</strong></span>
              <span class="deptname"><strong>科室</strong></span>
              <span class="weight"><strong>重量</strong></span>
              <span class="time"><strong>时间</strong></span>
              <el-button style="position: absolute; right: 185px;" icon="el-icon-folder-add" v-on:click="goto('/collectionList?deptid=' + deptid)">查看全部</el-button>
            </div>
            <div class="tabbody">
              <ul>
                <li v-for="dep in deplist">
                  <span class="code">{{dep.code}}</span>
                  <span class="type">{{dep.typeName}}</span>
                  <span class="deptname">{{dep.deptname}}</span>
                  <span class="weight">{{dep.weight}}</span>
                  <span class="time">{{dep.time}}</span>
                  <!--<el-button icon="el-icon-delete" v-on:click="deletes('')">删除</el-button>-->
                  <!--<el-button icon="el-icon-printer" @click="print()">打印</el-button>-->
                </li>
              </ul>
            </div>
          </div>
          <div class="depjj" @click="open">
            <i class="iconfont iconguanliliuzhuanxiezuo"></i>
            <div>科室交接</div>
          </div>
        </div>
        <ul class="lj clearFixed">
          <li class="c1"  @click="show(1)">
            <div class="icons">
              <i class="iconfont iconganranke"></i>
              <span>感染性</span>
              <transition name="el-fade-in-linear">
                <div v-show="show1">
                  <span class="big">{{NW}}KG</span>
                  <!--<el-button class="btn-print" v-bind:disabled="disabled" @click="getWeight(1)">称重</el-button>-->
                  <el-button class="btn-print" v-bind:disabled="disabled"  @click.stop="print(1)">打印标签</el-button>
                </div>
              </transition>
            </div>
          </li>
          <li class="c2" @click="show(2)">
            <div class="icons">
              <i class="iconfont iconsunhuai"></i>
              <span>损伤性</span>
              <transition name="el-fade-in-linear">
                <div v-show="show2">
                  <span class="big">{{NW}}KG</span>
                  <!--<el-button class="btn-print" v-bind:disabled="disabled" @click="getWeight(2)">称重</el-button>-->
                  <el-button class="btn-print" v-bind:disabled="disabled" @click.stop="print(2)">打印标签</el-button>
                </div>
              </transition>
            </div>
          </li>
          <li class="c3" @click="show(3)">
            <div class="icons">
              <i class="iconfont iconbingli"></i>
              <span>病理性</span>
              <transition name="el-fade-in-linear">
                <div v-show="show3">
                  <span class="big">{{NW}}KG</span>
                  <!--<el-button class="btn-print" :disabled="disabled" @click="getWeight(3)">称重</el-button>-->
                  <el-button class="btn-print" v-bind:disabled="disabled" v-on:click.stop="print(3)">打印标签</el-button>
                </div>
              </transition>
            </div>
          </li>
          <li class="c4" @click="show(4)">
            <div class="icons">
              <i class="iconfont iconhuaxueshiyan"></i>
              <span>化学性</span>
              <transition name="el-fade-in-linear">
                <div v-show="show4">
                  <span class="big">{{NW}}KG</span>
                  <!--<el-button class="btn-print" v-bind:disabled="disabled" @click="getWeight(4)">称重</el-button>-->
                  <el-button class="btn-print" v-bind:disabled="disabled" @click.stop="print(4)">打印标签</el-button>
                </div>
              </transition>
            </div>
          </li>
          <li class="c5" @click="show(5)">
            <div class="icons">
              <i class="iconfont iconyao"></i>
              <span>药物性</span>
              <transition name="el-fade-in-linear">
                <div v-show="show5">
                  <span class="big">{{NW}}KG</span>
                  <!--<el-button class="btn-print" v-bind:disabled="disabled" @click="getWeight(5)">称重</el-button>-->
                  <el-button class="btn-print" v-bind:disabled="disabled" @click.stop="print(5)">打印标签</el-button>
                </div>
              </transition>
            </div>
          </li>
        </ul>
      </div>
    </el-main>
  </div>
</template>

<script>
import myHeader from './header';
import myFooter from './footer';
import {_Widget} from "../common";
import {Downloader} from "../common";
import {printLable} from '../../static/mmprinter.window.js';
import { Loading } from 'element-ui';
export default {
  name: 'collection',
  components:{
    myHeader,
    myFooter,
  },
  data: () => ({
    disabled:false,
    pinSuccess:false,
    show1: false,
    show2: false,
    show3: false,
    show4: false,
    show5: false,
    operator:"",
    operatorId:null,//操作员id
    carNo: '02',
    changePersion:"",
    todayTotal_d1:{
      depname:"呼吸科",
      zzl:0,
      zsl:0,
      data:[]
    },
    deplist:[],
    deptname:"",
    deptid:"",
    NW:0,
  }),
  methods: {
    show(index){
      this.disabled = false;
      this.show1 = false;
      this.show2 = false;
      this.show3 = false;
      this.show4 = false;
      this.show5 = false;
      this["show" + index] = !this["show" + index];
      this.NW = 0;
      this.getWeight(index);
    },
    printLable,
    open() {
      var thisObject = this;
      this.$prompt('请扫描交接二维码', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消'
      }).then(({ value }) => {
        var url = thisObject.config.baseURL + "of_ryxx";
        url += "?hospitalCode=" + thisObject.config.hospitalCode;
        var para = {
          id:value
        };
        Downloader.loadXMLAsync(url + "&as_json=" + JSON.stringify(para),function(e) {
          var xmldoc = e.response;
          var obj = {};
          var xml = _Widget.xml.jsonToxml(xmldoc);
          var json = _Widget.xml.xmlToJson(xml);
          var res = (JSON.parse(json.string["#text"]));
          if(res.result == 1){
            thisObject.changePersion = res.name;
            localStorage.setItem("changePersion",res.name);
            document.dispatchEvent(window.changePersion)
            thisObject.$message({
              type: 'success',
              message: '交接人:' + res.name
            });
          }else{
            thisObject.$message({
              type: 'error',
              message: '获取交接人失败!'
            });
          }
        },function (err) {
          thisObject.$message({
            type: 'error',
            message: '网络链接失败!'
          });
          thisObject.changePersion = value;
          localStorage.setItem("changePersion",value);
          document.dispatchEvent(window.changePersion)
          thisObject.$message({
            type: 'success',
            message: '交接人:' + value
          });
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '取消输入'
        });
      });
    },
    deletes() {
      this.$confirm('此操作将删除该记录, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$message({
          type: 'success',
          message: '删除成功!'
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    getWeight(){
      //模拟蓝牙称数据
      /*this.NW = 3.02;
      return*/
      //打开蓝牙称，获取重量
      var thisObject = this;
      function call(data){
        console.log(data);
        //thisObject.NW = window.NW;第一次返回的数据是上次
      };
      function call2(data){
        console.log(data);
        thisObject.NW = window.NW;
      };
      window.printer.readNW(call);
      setTimeout(function (){
        window.printer.readNW(call2);
      },1000)
      //alert("call0 NW:" + window.NW)
      //return 1;
    },
    print(type){
      this.show1 = false;
      this.show2 = false;
      this.show3 = false;
      this.show4 = false;
      this.show5 = false;
      var thisObject = this;
      if(!thisObject.changePersion){
        this.open();
        return;
      }
      if(!thisObject.NW ){
        this.$message({
          message: '请先称重',
          type: 'error'
        });
        return;
      }
      if(parseFloat(thisObject.NW) <= 0){
        this.$message({
          message: '重量错误',
          type: 'error'
        });
        return;
      }
      var name = thisObject.operator;//操作员
      var time = new Date().getTime();
      //var weight = thisObject.getWeight();
      var code = _Widget.getCode("01",type,thisObject.carNo,time);
      var t = _Widget.dateUtil.formatDateTime2(time,'yyyy-MM-dd HH:mm:ss');
      var url = thisObject.config.baseURL + "of_yf_tj";
      url += "?hospitalCode=" + thisObject.config.hospitalCode;
      /*var thisPrintCode = parseInt(code.slice(5));
      var locPrintCode = localStorage.getItem("printCode");
      if(!locPrintCode){
        localStorage.setItem("printCode",thisPrintCode);
      }else{
        //避免重复打印，三秒内不能再次打印
        if(thisPrintCode - locPrintCode > 3){
          this.$message({
            message: '不可重复打印',
            type: 'error'
          });
          return;
        }
      }*/
      var para = {
        type:"01",//采集01 核交02 入库03 异常04
        id:thisObject.operatorId,
        carNo:thisObject.config.carId,
        weight:thisObject.NW,
        code:code,
        changePersion:thisObject.changePersion,
        wpType:type,
        deptid:thisObject.deptid,
        time:t
      };
      Downloader.loadXMLAsync(url + "&as_json=" + JSON.stringify(para),function(e) {
        var xmldoc = e.response;
        var obj = {};
        var xml = _Widget.xml.jsonToxml(xmldoc);
        var json = _Widget.xml.xmlToJson(xml);
        var res = (JSON.parse(json.string["#text"]));
        if(res.result == 1){
          thisObject.$message({
            message: '提交完成',
            type: 'success'
          });
          thisObject.getdeptData();
        }
      },function (err) {
        thisObject.$message({
          type: 'error',
          message: '网络链接失败!已记录本地数据库，请网络连接成功后手动提交！'
        });
        var todayTotal_d1 = localStorage.getItem("todayTotal_d1");
        if(todayTotal_d1){
          thisObject.todayTotal_d1 = JSON.parse(todayTotal_d1);
        }

        //没网，加入本地数据库
        var waitCollection = localStorage.getItem("waitCollection");
        waitCollection = waitCollection || [];
        if(typeof waitCollection == "string"){
          waitCollection = JSON.parse(waitCollection);
        }
        waitCollection.push(para);
        localStorage.setItem("waitCollection",JSON.stringify(waitCollection))
        document.dispatchEvent(window.waitCollectionUpdate);
      })
      var typeName = ""
      switch (type){
        case 1:
          typeName = "感染性";
          break;
        case 2:
          typeName = "损伤性";
          break;
        case 3:
          typeName = "病理性";
          break;
        case 4:
          typeName = "化学性";
          break;
        case 5:
          typeName = "药物性";
          break;
        default:
      }
      var params = {
        deptname: "科室：" + thisObject.deptname,
        time: "时间：" + t,
        type: typeName,
        weight: "重量：" + thisObject.NW + "KG",
        hospital: "医院：" + thisObject.config.hospitalName,
        code: code,
        person: '采集人：' + thisObject.operator
      };
      this.printLable(params);
      thisObject.disabled = true;
    },
    goto(url){
      this.$router.push(url)
    },
    /*打印：类型：采集 + 废物类型 + 车号 + 编号（001，002...）*/
    getdeptData(){
      var thisObject = this;
      //thisObject.loadingInstance = Loading.service({ fullscreen: false });
      var url = thisObject.config.baseURL + "of_cj_sy";
      url += "?hospitalCode=" + thisObject.config.hospitalCode + "&depcode=" + thisObject.deptid;
      Downloader.loadXMLAsync(url, function(e) {
        var xmldoc = e.response;
        var obj = {};
        var xml = _Widget.xml.jsonToxml(xmldoc);
        var json = _Widget.xml.xmlToJson(xml);
        var res = (JSON.parse(json.string["#text"]));
        if(res.result == 1){
          thisObject.todayTotal_d1 = res;
          res.data.forEach(function (v){
            switch (v.type){
              case 1:
                v.typeName = "感染性";
                break;
              case 2:
                v.typeName = "损伤性";
                break;
              case 3:
                v.typeName = "病理性";
                break;
              case 4:
                v.typeName = "化学性";
                break;
              case 5:
                v.typeName = "药物性";
                break;
              default:
            }
          })
          thisObject.deplist = res.data;
          localStorage.setItem("todayTotal_d1",JSON.stringify(res));
          //thisObject.loadingInstance.close();
        }
      },function (err) {
        thisObject.$message({
          type: 'error',
          message: '网络链接失败!'
        });
        var todayTotal_d1 = localStorage.getItem("todayTotal_d1");
        if(todayTotal_d1){
          thisObject.todayTotal_d1 = JSON.parse(todayTotal_d1);
        }
      })
    },
    onkeydown(e){
      var thisObject = this;
      if(e.keyCode == 13){
        var url = thisObject.config.baseURL + "of_ryxx";
        url += "?hospitalCode=" + thisObject.config.hospitalCode;
        var para = {
          id:thisObject.keyval
        };
        Downloader.loadXMLSync(url + "&as_json=" + JSON.stringify(para),function(e) {
          var xmldoc = e.response;
          var obj = {};
          var xml = _Widget.xml.jsonToxml(xmldoc);
          var json = _Widget.xml.xmlToJson(xml);
          var res = (JSON.parse(json.string["#text"]));
          if(res.result == 1){
            thisObject.changePersion = res.name;
            localStorage.setItem("changePersion",res.name);
            document.dispatchEvent(window.changePersion)
            thisObject.$message({
              type: 'success',
              message: '交接人:' + res.name
            });
          }else{
            thisObject.$message({
              type: 'error',
              message: '获取交接人失败!'
            });
          }
        },function (err) {
          thisObject.$message({
            type: 'error',
            message: '网络链接失败!'
          });
          thisObject.changePersion = thisObject.keyval;
          localStorage.setItem("changePersion",thisObject.keyval);
          document.dispatchEvent(window.changePersion)
          thisObject.$message({
            type: 'success',
            message: '交接人:' + thisObject.keyval
          });
        });
        thisObject.keyval = '';
        return
      }
      thisObject.keyval += String.fromCharCode(e.keyCode);
    }
  },
  mounted() {
    var thisObject = this;
    this.config = JSON.parse(localStorage.getItem("config"));
    this.operator = localStorage.getItem("operator");
    this.operatorId = localStorage.getItem("operatorId");
    this.deptid = this.$route.query.deptid;
    this.deptname = this.$route.query.deptname;
    this.changePersion = localStorage.getItem("changePersion") || '';
    this.getdeptData();
    this.keyval = "";
    document.addEventListener("keydown",this.onkeydown);
  },
  destroyed(){
    this.keyval = "";
    document.removeEventListener("keydown",this.onkeydown);
    localStorage.setItem("changePersion","");
  }
}
</script>



