<template>
  <div>
    <my-header></my-header>
    <el-main>
      <div id="updataDetailPage" class="clearFixed">
        <el-card class="errList">
          <el-table v-loading="loading" :data="tableData" style="width: 100%;height:460px">
            <el-table-column prop="index" label="序号" width="60"></el-table-column>
            <el-table-column prop="num" label="标签条码" width="180"></el-table-column>
            <el-table-column prop="address" label="污秽间" width="180"></el-table-column>
            <el-table-column prop="type" label="类型" width="180"></el-table-column>
            <el-table-column prop="weight" label="重量" width="100"></el-table-column>
            <el-table-column prop="unit" label="单位" width="100"></el-table-column>
            <el-table-column prop="date" label="时间" width="180"></el-table-column>
            <el-table-column fixed="right" label="操作">
              <template slot-scope="scope">
                <el-button v-on:click="deletes('')" type="text" size="small">删除</el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-card>
      </div>
    </el-main>
    <my-footer :active="active"></my-footer>
  </div>
</template>

<script>
import myHeader from './header';
import myFooter from './footer';
import {baseURL} from '../common.js';
import  axios from 'axios';
export default {
  name: 'updataDetail',
  components:{
    myHeader,
    myFooter,
  },
  data: () => ({
    active:'/error',
    car: '',
    doctor:'',
    loading: false,//数据接口完成后打开
    seltype:0,//选中序列默认值
    selErrtype:0,//选中的类型
    tableData: [],

  }),
  methods: {
    selType: function (index,errtype) {
      this.seltype = index;
      this.selErrtype = errtype;
      this.getData(this.selErrtype)
    },
    goto(url){
      this.$router.push(url)
    },
    handleClick(res){

    },
    getData(seltype){
      var url = baseURL + "targetIDToPoiID?bdid=B000A11DDU&targetID=" + seltype
      axios.get(url)
        .then(function (res) {
          console.log(res);
          if(res.data.result == 1){
            this.$data.tableData = res.data;
          }
        })
        .catch(function (error) {
          console.log(error);
        })
    },
    deletes() {
      this.$confirm('此操作将删除该记录, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$message({
          type: 'success',
          message: '删除成功!'
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
  },
  mounted(){
    this.config = JSON.parse(localStorage.getItem("config"));
  }
}
</script>



