<!--生成科室二维码注意
  {"id":"2","password":"2"} 必须为双引号
-->
<template>
  <div id="loginPage">
    <el-tabs v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="扫码登录" name="first" style="text-align: center">
        <!--<input type="text" id="codeInput" v-model="code"  v-on:input="input($event)" v-focus ref="input">-->
        <input type="text" id="codeInput" v-model="code" v-focus ref="input" style="visibility: hidden">
        <div><img src="../assets/scan.gif" width="200" alt=""></div>
        <div>请用扫码枪扫描登录人二维码</div>
      </el-tab-pane>
      <el-tab-pane label="账号登录" name="second">
        <el-row type="flex" justify="center">
          <el-col :span="12" style="width: 100%">
            <el-form
              label-position="left"
              label-width="80px"
              :model="formLogin"
              :rules="rules"
              ref="formLogin">
              <!-- $refs 只在组件渲染完成后才填充，并且它是非响应式的。它仅仅作为一个直接访问子组件的应急方案——应当避免在模版或计算属性中使用 $refs 。 -->
              <el-form-item label="账号" prop="id">
                <el-input v-model="formLogin.id" data-mode="en_cap"></el-input>
              </el-form-item>
              <el-form-item label="密码" prop="password">
                <el-input v-model="formLogin.password" data-mode="en_cap" show-password></el-input>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="login" style="width: 300px">登录</el-button>

              </el-form-item>
              <!--<el-form-item>
                <router-link to="/register">
                  <el-button type="">没有账号，立即注册<i class="el-icon-arrow-right el-icon&#45;&#45;right"></i></el-button>
                </router-link>
              </el-form-item>-->
            </el-form>
          </el-col>
        </el-row>
      </el-tab-pane>
    </el-tabs>
    <vue-dusion-keyboard ref="zzz" scope=".scope-test" @keyvalue="kkk" @del="del" style="left:0px;width: 100%;"></vue-dusion-keyboard>
  </div>
</template>

<script type="text/javascript">
  // 引入vuex /src/helper.js中的辅助函数，
  // 将actions中的方法直接转为组件中的方法
  import {mapActions} from 'vuex'
  import {_Widget, Downloader} from "../common";
  import {VueDusionKeyboard,Paint} from 'vue-dusion-keyboard';
  export default {
    components:{
      VueDusionKeyboard,
      Paint,
    },
    data(){
      let checkUserName = (rule,value,cb)=>{
        if(!value){
          return cb(new Error('账户不能为空!'))
        }else{
          cb(); // 将判断传递给后面
        }

      }
      let checkPassword = (rule,value,cb)=>{
        if(!value){
          return cb(new Error('密码不能为空!'))
         }else{
          cb();
         }
      }
      return{
        isCanScanLogin:true,
        activeName: 'first',
        formLogin:{
          id: '',
          password: '',
          checkPassword: ''
        },
        rules:{
          name:[
            {validator:checkUserName,trigger: 'blur'}
          ],
          password:[
            {validator:checkPassword,trigger: 'blur'}
          ],
        },
        code:""
      }
    },
    methods:{
      /*input(event){
        var value = event.currentTarget.value;
        this.scanLogin(value);
      },*/
      kkk(kk) {
        console.log(kk);
      },
      del(text, index) {
        console.log(text);
        console.log(index);
      },
      handleClick(res){
        if(res.index == 0){
          this.code = '';
          this.$refs['input'].focus()
        }
      },
      login(){
        var thisObject = this;
        let user = this.formLogin;
        let formData = {
          id: user.id,
          password: user.password
        };
        // 表单验证
        this.$refs['formLogin'].validate((valid) => {
          if (valid) {
            // 通过验证之后才请求登录接口
            thisObject.scanLogin(formData);
          } else {
            this.$message.error('表单验证失败!')
            return false;
          }
        });
      },
      scanLogin(formData){
        var thisObject = this;
        thisObject.$message({
          type: 'success',
          message: res.name +' 登录成功!'
        });
        thisObject.$router.push('/');
        if(formData.id && formData.password){
          var url =  thisObject.config.baseURL + "of_dl";
          url += "?hospitalCode=" + thisObject.config.hospitalCode;
          Downloader.loadXMLAsync(url + "&as_json=" + JSON.stringify(formData),function(e){
            var xmldoc = e.response;
            var obj = {};
            var xml = _Widget.xml.jsonToxml(xmldoc);
            var json = _Widget.xml.xmlToJson(xml);
            var res = (JSON.parse(json.string["#text"]));
            if(res.result == 1){
              thisObject.operator = res.name;
              thisObject.operatorId = formData.id;
              localStorage.setItem("operator",res.name);
              localStorage.setItem("operatorId",formData.id);
              thisObject.$message({
                type: 'success',
                message: res.name +' 登录成功!'
              });
              thisObject.$router.push('/');
            }else{
              thisObject.$message({
                type: 'success',
                message: '用户名或密码错误!'
              });
            }
          },function (err) {
            thisObject.$message({
              type: 'error',
              message: '网络链接失败!'
            });
          });
        }
      },
      // 表单重置
      resetForm(){
        console.log('session')
        this.$refs['formLogin'].resetFields();
      },
      onkeydown(e){
        var thisObject = this;
        if(e.keyCode == 13){
          console.log("toLocaleLowerCase :" + thisObject.keyval.toLocaleLowerCase());
          var code =  thisObject.keyval.toLocaleLowerCase();
          thisObject.scanLogin({
            id: code.split("p")[0],
            password: code.split("p")[1]
          });
          thisObject.keyval = '';
          return
        }
        thisObject.keyval += String.fromCharCode(e.keyCode);
      }
    },
    mounted(){
      var thisObject = this;
      this.config = JSON.parse(localStorage.getItem("config"));
      this.keyval = "";
      document.addEventListener("keydown",this.onkeydown);
    },
    destroyed(){
      this.keyval = "";
      document.removeEventListener("keydown",this.onkeydown)
    }
  }
</script>



