// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import '../static/css/style.css';
import '../static/css/iconfont.css';
import App from './App.vue';
import router from './router';
import './directive';
//windows调用方式
import {printer} from "../static/mmprinter.window";
import {_Widget, Downloader} from "./common";
Vue.config.productionTip = false
window.document.addEventListener('deviceready', function () {
  new Vue({
    el: '#app',
    router,
    components: { App },
    template: '<App/>',
    mounted() {
      var thisObject = this;
      thisObject.config = JSON.parse(localStorage.getItem("config"));
      window.bluetoothOnEvet = document.createEvent("HTMLEvents");
      window.bluetoothOffEvet = document.createEvent("HTMLEvents");
      window.scanDeviceSuccess = document.createEvent("HTMLEvents");
      window.contentSuccess = document.createEvent("HTMLEvents");
      window.waitCollectionUpdate = document.createEvent("HTMLEvents");
      window.bluetoothOnEvet.initEvent("bluetoothOn", false, false);
      window.bluetoothOffEvet.initEvent("bluetoothOff", false, false);
      window.scanDeviceSuccess.initEvent("scanDeviceSuccess", false, false);
      window.contentSuccess.initEvent("contentSuccess", false, false);
      window.waitCollectionUpdate.initEvent("waitCollectionUpdate", false, false);

      document.addEventListener("bluetoothOn",function (e){
        localStorage.setItem("isBluetooth",'true');
      });
      document.addEventListener("bluetoothOff",function (e){
        localStorage.setItem("isBluetooth",'false');
      })
      //alert("initEvent")
      window.setTimeout(function (){
        window.printer.init();
      },1000);
    }
  });

});
