<template>
  <div>
    <my-header></my-header>
    <el-main>
      <div id="outRoomPage" class="clearFixed">
        <div class="waitout">
          <div class="title clearFixed">
            <span>待出库</span>
            <!--<el-button>全部复核</el-button>-->
          </div>
          <div class="con">
            <div>
              <el-form ref="form" :model="form1" label-width="80px">
                <el-form-item label="周转箱">
                  <el-input v-model="form1.num" disabled></el-input>箱
                  <el-input class="bigger" v-model="form1.kg" disabled></el-input>KG
                </el-form-item>
              </el-form>
            </div>
          </div>
          <div class="detail">
            <el-table v-loading="loading" :data="tableData" style="width: 100%;max-height:473px">
              <el-table-column prop="code" label="编码" width="240"></el-table-column>
              <el-table-column prop="weight" label="重量"></el-table-column>
            </el-table>
          </div>
        </div>
        <div class="outed">
          <div class="title  clearFixed">
            <span>出库单</span>
            <el-button @click="submits">确认出库</el-button>
          </div>
          <div class="con">
            <div>
              <el-form ref="form" :model="form2" label-width="80px">
                <el-form-item label="周转箱">
                  <el-input v-model="form2.num" disabled></el-input>箱
                  <el-input class="bigger" v-model="form2.kg" disabled></el-input>KG
                </el-form-item>
              </el-form>
            </div>
            <!--<div>
              <el-row>
                <el-col :span="12">
                  <div class="grid-content">
                    出库单号：<span>-</span>
                  </div>
                </el-col>
                <el-col :span="12">
                  <div class="grid-content">
                    出库时间：<span>-</span>
                  </div>
                </el-col>
              </el-row>
            </div>-->
          </div>
          <div class="detail">
            <el-form :inline="true" :model="input" class="demo-form-inline" @submit.native.prevent>
              <el-form-item label="条码">
               <!-- <el-input v-model="input.code" :inputmode="inputCode(input.code)" placeholder="条码"></el-input>-->
                <el-input v-model="input.code"  placeholder="条码" disabled></el-input>
              </el-form-item>
            </el-form>
            <el-table v-loading="loading" :data="tempDate" style="width: 100%;max-height:400px">
              <el-table-column prop="code" label="编码" width="240"></el-table-column>
              <el-table-column prop="weight" label="重量"></el-table-column>
            </el-table>
          </div>
        </div>
      </div>
      <el-dialog
        :title="'确认出库'"
        style="text-align:left !important"
        :visible.sync="dialogVisible"
        :before-close="handleClose">
        <el-form ref="form" label-width="80px">
          <el-row class="demo-autocomplete">
            <el-col :span="12">
              <div class="sub-title">车牌号</div>
              <el-autocomplete
                class="inline-input"
                v-model="state1"
                :fetch-suggestions="querySearch"
                placeholder="请输入内容"
                @select="handleSelectCarNo"
                value-key="value"
              ></el-autocomplete>
            </el-col>
            <el-col :span="12">
              <div class="sub-title">接收人员</div>
              <el-autocomplete
                class="inline-input"
                v-model="state2"
                :fetch-suggestions="querySearchName"
                placeholder="请输入内容"
                @select="handleSelectDriver"
                value-key="driver"
              ></el-autocomplete>
            </el-col>
          </el-row>
        </el-form>
        <span slot="footer" class="dialog-footer">
        <el-button type="success" @click="save()">提交</el-button>
        <el-button type="primary" @click="dialogVisible = false">取消</el-button>
      </span>
      </el-dialog>
    </el-main>
    <my-footer></my-footer>
  </div>
</template>

<script>
import myHeader from './header';
import myFooter from './footer';
import {_Widget, Downloader} from "../common";
export default {
  name: 'outRoom',
  components:{
    myHeader,
    myFooter,
  },
  data: () => ({
    loading:false,
    dialogVisible: false,
    addFlag:false,
    restaurants: [],
    state1: '',
    state2: '',
    operatorId:null,//操作员ID
    license:null,//收集车车号
    driver:"",
    obj:{
      weight:0
    },
    input:{
      code:null,
      weight:0
    },
    tableData: [],
    tempDate:[],
    tableData2:[],
    form1:{
      num:0,
      kg:0
    },
    form2:{
      num:0,
      kg:0
    }
  }),
  methods: {
    logout: function () {

    },
    setting: function (){

    },
    handleClose(done) {
      //done();
    },
    querySearch(queryString, cb) {
      var restaurants = this.restaurants.carNo;
      var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    querySearchName(queryString, cb) {
      var restaurants = this.restaurants.driver;
      var results = queryString ? restaurants.filter(this.createFilterName(queryString)) : restaurants;
      // 调用 callback 返回建议列表的数据
      cb(results);
    },
    createFilter(queryString) {
      return (restaurant) => {
        return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
      };
    },
    createFilterName(queryString) {
      return (restaurant) => {
        return (restaurant.name.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
      };
    },
    getCarInfo() {
      var thisObject = this;
      var url = thisObject.config.baseURL + "of_ck_txxx";
      Downloader.loadXMLAsync(url,function(e){
        var xmldoc = e.response;
        var obj = {};
        var xml = _Widget.xml.jsonToxml(xmldoc);
        var json = _Widget.xml.xmlToJson(xml);
        var res = (JSON.parse(json.string["#text"]));
        if(res.result == 1){
          thisObject.restaurants = {
            carNo:res.carNo,
            driver:res.driver
          };
        }
      },function (err) {
        thisObject.$message({
          type: 'error',
          message: '网络链接失败!'
        });
      });

    },
    handleSelectCarNo(item) {
      this.license = item;
    },
    handleSelectDriver(item) {
      this.driver = item;
    },
    submits() {
      if(this.tempDate.length == 0){
        this.$message({
          type: 'error',
          message: '请选择出库项!'
        });
        return;
      }
      this.dialogVisible = true;
      this.addFlag = false;
    },
    save(){
      var thisObject = this;
      var url = thisObject.config.baseURL + "of_yf_tj";
      url += "?hospitalCode=" + thisObject.config.hospitalCode;
      var time = new Date().getTime();
      var t = _Widget.dateUtil.formatDateTime2(time,'yyyy-MM-dd HH:mm:ss');
      var tData = {
        type: "04",
        id: thisObject.operatorId,
        time:t,
        carNo:thisObject.license,
        driver:thisObject.driver,
        data:thisObject.tempDate
      };
      Downloader.loadXMLAsync(url + "&as_json=" + JSON.stringify(tData),function(e){
        var xmldoc = e.response;
        var obj = {};
        var xml = _Widget.xml.jsonToxml(xmldoc);
        var json = _Widget.xml.xmlToJson(xml);
        var res = (JSON.parse(json.string["#text"]));
        if(res.result == 1){
          thisObject.tempDate = [];
          thisObject.form2.num = 0;
          thisObject.form2.kg = 0;
          thisObject.input.code = "";
          thisObject.$message({
            type: 'success',
            message: '出库成功!'
          });
        }
      },function (err) {
        thisObject.$message({
          type: 'error',
          message: '网络链接失败!'
        });
      });
      this.dialogVisible = false;
    },
    getData1(){//未出库
      var thisObject = this;
      var url = thisObject.config.baseURL + "of_wck_mx";
      url += "?hospitalCode=" + thisObject.config.hospitalCode;
      Downloader.loadXMLAsync(url,function(e){
        var xmldoc = e.response;
        var obj = {};
        var xml = _Widget.xml.jsonToxml(xmldoc);
        var json = _Widget.xml.xmlToJson(xml);
        var res = (JSON.parse(json.string["#text"]));
        if(res.result == 1){
          res.data.forEach(function (v){
            switch (v.type){
              case 1:
                v.typeName = "感染性";
                break;
              case 2:
                v.typeName = "损伤性";
                break;
              case 3:
                v.typeName = "病理性";
                break;
              case 4:
                v.typeName = "化学性";
                break;
              case 5:
                v.typeName = "药物性";
                break;
              default:
            }
          });
          thisObject.tableData = res.data;
          thisObject.form1.num = res.zsl;
          thisObject.form1.kg = res.zzl;
        }
      },function (err) {
        thisObject.$message({
          type: 'error',
          message: '网络链接失败!'
        });
      });
    },
    /*getData2(){//已出库
      var thisObject = this;
      var url = baseURL + "of_yck_mx";
      Downloader.loadXMLAsync(url,function(e){
        var xmldoc = e.response;
        var obj = {};
        var xml = _Widget.xml.jsonToxml(xmldoc);
        var json = _Widget.xml.xmlToJson(xml);
        var res = (JSON.parse(json.string["#text"]));
        if(res.result == 1){
          res.data.forEach(function (v){
            switch (v.type){
              case 1:
                v.typeName = "感染性";
                break;
              case 2:
                v.typeName = "损伤性";
                break;
              case 3:
                v.typeName = "病理性";
                break;
              case 4:
                v.typeName = "化学性";
                break;
              case 5:
                v.typeName = "药物性";
                break;
              default:
            }
          });
          thisObject.tableData2 = res.data;
          thisObject.form2.num = res.zsl;
          thisObject.form2.kg = res.zzl;
        }
      },function (err) {
        thisObject.$message({
          type: 'error',
          message: '网络链接失败!'
        });
      });
    },*/
    inputCode(value){
      var thisObject = this;
      if(value && this.tableData.length){
        thisObject.tableData.forEach(function (v,index){
          if(v.code == value){
            thisObject.tableData.splice(index,1);
            thisObject.tempDate.push(v);
            parseInt(thisObject.form1.num);
            parseInt(thisObject.form2.num);
            --thisObject.form1.num;
            ++thisObject.form2.num;
            thisObject.form1.kg = Number((thisObject.form1.kg - Number(parseFloat(v.weight).toFixed(2))).toFixed(2));
            thisObject.form2.kg = Number((thisObject.form2.kg + Number(parseFloat(v.weight).toFixed(2))).toFixed(2));
          }
        })
      }
    },
    onkeydown(e){
      var thisObject = this;
      if(e.keyCode == 13){
        thisObject.input.code = thisObject.keyval;
        thisObject.inputCode(thisObject.keyval);
        thisObject.keyval = '';
        return
      }
      thisObject.keyval += String.fromCharCode(e.keyCode);
    }
  },

  mounted() {
    this.config = JSON.parse(localStorage.getItem("config"));
    this.operator = localStorage.getItem("operator");
    this.operatorId = localStorage.getItem("operatorId");
    this.getCarInfo();
    this.getData1();
    this.keyval = "";
    document.addEventListener("keydown",this.onkeydown);
  },
  destroyed(){
    this.keyval = "";
    document.removeEventListener("keydown",this.onkeydown)
  }
}
</script>



